<template>
  <div  v-el:panel role="tabpanel" class="tab-pane active" :class="[animate]" v-if="show">
    <slot></slot>
  </div>

</template>
<script>
import $ from 'jquery'
import coerceBoolean from '../utils/coerceBoolean.js'
export default {
    props: {
        header: {
            type: String
        },
        disabled: {
            type: Boolean,
            coerce: coerceBoolean,
            default: false
        },
        animate: {
            type: String,
            default: 'fade'
        },
        href: {
            type: String,
            default: null
        }
    },
    computed: {
        // tab, 
        active () {
            return this._tabset.show === this
        },

        //  index
        index () {
            return this._tabset.tabs.indexOf(this)
        },

        // 
        show () {
            this.tabShow = this._tabset && this._tabset.show === this;
            return this.tabShow;
        }
    },
    data () {
        return {
            tabShow: false
        }
    },
    created () {
        // tab  tabgroup
        this._ingroup = this.$parent && this.$parent._tabgroup
        let tabset = this
        //  tabset 
        while (tabset && tabset._tabset !== true && tabset.$parent) {
            tabset = tabset.$parent
        }

        // tabset
        if (!tabset._tabset) {
            this._tabset = {}
            console.warn('Warning: "tab" depend on "tabset" to work properly.')
        } else {
            // push tab
            tabset.tabs.push(this)

            // group
            if (!this._ingroup) {
                tabset.headers.push(this)
            } else {
                //  tabgroup tab
                //  tabgroup tabset
                // pushtabgroup
                if (!~tabset.headers.indexOf(this.$parent)) {
                    tabset.headers.push(this.$parent)
                }
            }
            //  tabset
            this._tabset = tabset
        }

        //  tabgroup , push  tabgroup  tab
        if (this._ingroup) {
            this.$parent.tabs.push(this)
        }
        if (!this.header) {
            console.warn('Warning: "tab" unset attribute header or header is empty ');
        }
    },
    watch: {
        tabShow (val) {
            if (val) {
                this.animate ? this.$els.panel.offsetWidth && $(this.$els.panel).addClass('in')
                : $(this.$els.panel).addClass('in');
            } else {
                $(this.$els.panel).removeClass('in');
            }
        }
    },
    // 
    beforeDestroy () {
        //  tab
        // active
        if (this._tabset.active === this.index) {
            this._tabset.active = 0
        }
        // tab
        if (this._ingroup) {
            this.$parent.tabs.$remove(this);
        }
        // tabset
        this._tabset.tabs.$remove(this)
    }
}
</script>



